*{
  padding: 0;
  margin: 0;
}

.container {
  /* background-color: brown; */
  display: flex;
  margin: 0;
  justify-content: center;
  align-items: center;
  height: 140vh;
}

.form {
  /*background-color: orange;*/
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(#244BC5,#1d59ff);
}

.form label{
  color: #FFF;
  font-size: 20px;
  font-weight: light;
  font-family: Poppins;
}

.form input{
  color: #FFF;
  font-size: 20px;
  font-family: Poppins;
  background-color: transparent;
  border: 1px solid #FFF;
  border-radius: 8px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: 10px;
}

.form input::placeholder{
  color: #FFF;
  font-size: 12px;
  opacity: 0,7;
}

.form button {
  color: #244BC5;
  font-size: 20px;
  font-family: Poppins;
  border: 30px;
  background-color: #FFF;
  border-radius: 8px;
  width: 100%;
  height: 50px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  cursor: pointer;
}


.container-input-Label {
  /* background-color: green; */
  height: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.children-container-input-Label {
  /* background-color: cyan; */
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 8px;
}

.container-button{
  /* background-color: orange; */
  display: flex;
  flex-direction: column;
  width: 250px;
  height: 30%;
  justify-content: center;
  
}